<template>
  <v-container>
    <v-row>{{result.title}}</v-row>
  </v-container>
</template>

<script>
export default {
  name: "Homedet",
  data: () => ({
    result: {},
  }),
  methods: {
    getdata() {
      const baseURI = `https://jsonplaceholder.typicode.com/photos/${this.$route.params.id}`;
      this.$axios.get(baseURI).then((result) => {
        this.result = result.data;
      });
    },
  },
  mounted() {
    this.getdata();
  },
};
</script>